import React from 'react';
import {
     Switch,
     Route
   } from "react-router-dom";
   import ArrowLink from '../UI/ArrowLink';
   import {SectionWrapper,SectionHeading,SectionLinks,LinkText,LinkItemWrapper,Section,LinkItem} from '../Styles/SectionStyles';
   import Option1 from './Option1';
  //  import Option2 from './Option2';
  //  import Option3 from './Option3';

const Section2 = () => {

  return (
    <SectionWrapper>
      <Section>
        <SectionHeading>Анализ финансовой устойчивости</SectionHeading>
        <SectionLinks>
          <LinkItemWrapper>
            <LinkItem to="/section1/option1">
              <LinkText>Коэффициент автономии</LinkText>
            </LinkItem>
          </LinkItemWrapper>
        </SectionLinks>
     </Section>
      <Switch>
        <Route path="/section1/option1"> 
               <Option1/>
        </Route>
      </Switch>
    </SectionWrapper>
  );
} 

export default Section2;
