import React,{useState} from 'react';
import {
     BrowserRouter as Router,
     Switch,
     Route
   } from "react-router-dom";
   import MainLogo from '../UI/MainLogo';
   import MenuTrigger from '../UI/MenuTrigger';
import Section1 from '../Section1';
import Section2 from '../Section2';
import {Wrapper,MainNavigationLink,MobileMenuClosed,MobileMenu,Navigation,LogoText,NavigationLink,AllLinksWrapper,NavigationLinkItem} from './styles';

const NavBar = () => {
  const [isMenuActive, setIsMenuActive] = useState(false);

  const menuHandler = () => {
    if(isMenuActive){
      setIsMenuActive(false);
    }else{
      setIsMenuActive(true);
    }
    console.log('menu' + isMenuActive);
  }

     return (
     <Router>
      <div>
        <Wrapper>
          <Navigation>
            <NavigationLinkItem>
              <MainNavigationLink to="/">
                <MainLogo/>
                <LogoText>Справочник финансиста</LogoText>
              </MainNavigationLink>
            </NavigationLinkItem>
              <MenuTrigger onClick={menuHandler}/>

              {isMenuActive ? 
                <MobileMenu>
                <NavigationLinkItem onClick={menuHandler}>
                  <NavigationLink to="/section1">Анализ финансовой устойчивости</NavigationLink>
                </NavigationLinkItem>
                <NavigationLinkItem onClick={menuHandler}>
                  <NavigationLink to="/section2">Анализ ликвидности</NavigationLink>
                </NavigationLinkItem>
                <NavigationLinkItem onClick={menuHandler}>
                  <NavigationLink to="/section3">Анализ финансовых результатов</NavigationLink>
                </NavigationLinkItem>
                <NavigationLinkItem onClick={menuHandler}>
                  <NavigationLink to="/section4">Анализ оборачиваемости и основных средств</NavigationLink>
                </NavigationLinkItem>
                <NavigationLinkItem onClick={menuHandler}>
                  <NavigationLink to="/section5">Финансовый анализ</NavigationLink>
                </NavigationLinkItem>
                <NavigationLinkItem onClick={menuHandler}>
                  <NavigationLink to="/section6">Нормативные методики финансового анализа</NavigationLink>
                </NavigationLinkItem>
              </MobileMenu>
              : <MobileMenuClosed/>
              }

            <AllLinksWrapper>
              <NavigationLinkItem>
                <NavigationLink to="/section1">Анализ финансовой устойчивости</NavigationLink>
              </NavigationLinkItem>
              <NavigationLinkItem>
                <NavigationLink to="/section2">Анализ ликвидности</NavigationLink>
              </NavigationLinkItem>
              <NavigationLinkItem>
                <NavigationLink to="/section3">Анализ финансовых результатов</NavigationLink>
              </NavigationLinkItem>
              <NavigationLinkItem>
                <NavigationLink to="/section4">Анализ оборачиваемости и основных средств</NavigationLink>
              </NavigationLinkItem>
              <NavigationLinkItem>
                <NavigationLink to="/section5">Финансовый анализ</NavigationLink>
              </NavigationLinkItem>
              <NavigationLinkItem>
                <NavigationLink to="/section6">Нормативные методики финансового анализа</NavigationLink>
              </NavigationLinkItem>
            </AllLinksWrapper>
          </Navigation>
        </Wrapper>
        
        <Switch>
          <Route path="/section1">
               <Section1/>
          </Route>
          <Route path="/section2">
            <Section2/>
          </Route>
          <Route path="/section3">
               <h1>Section 3</h1>
          </Route>
          <Route path="/section4">
               <h1>Section 4</h1>
          </Route>
          <Route path="/section5">
               <h1>Section 5</h1>
          </Route>
          <Route path="/section6">
               <h1>Section 6</h1>
          </Route>
          <Route path="/">
               <h1>Home</h1>
          </Route>
        </Switch>
      </div>
    </Router>
     )
}

export default NavBar;