import React from 'react';
import {
     Switch,
     Route
   } from "react-router-dom";
   import ArrowLink from '../UI/ArrowLink';
   import {SectionWrapper,SectionHeading,SectionLinks,LinkText,LinkItemWrapper,Section,LinkItem} from '../Styles/SectionStyles';
   import Option1 from './Option1';
   import Option2 from './Option2';
   import Option3 from './Option3';

const Section2 = () => {

  return (
    <SectionWrapper>
      <Section>
        <SectionHeading>Анализ ликвидности</SectionHeading>
        <SectionLinks>
          <LinkItemWrapper>
            <LinkItem to="/section2/option1">
              <LinkText>Коэффициент абсолютной ликвидности</LinkText>
            </LinkItem>
          </LinkItemWrapper>
          <LinkItemWrapper>
            <LinkItem to="/section2/option2">
              <LinkText>Коэффициент быстрой ликвидности</LinkText>
            </LinkItem>
          </LinkItemWrapper>
          <LinkItemWrapper>
            <LinkItem to="/section2/option3">
              <LinkText>Коэффициент обеспеченности собственными средствами</LinkText>
            </LinkItem>
          </LinkItemWrapper>
        </SectionLinks>
     </Section>
      <Switch>
        <Route path="/section2/option3">
               <Option3/>
        </Route>
        <Route path="/section2/option2">
               <Option2/>
        </Route>
        <Route path="/section2/option1"> 
               <Option1/>
        </Route>
      </Switch>
    </SectionWrapper>
  );
} 

export default Section2;
