import React from 'react';
import styled from 'styled-components';

const ArrowLink = () => {
     return (
          <Arrow xmlns="http://www.w3.org/2000/svg" width="30" height="30" viewBox="0 0 24 24"><path d="M12 2c5.514 0 10 4.486 10 10s-4.486 10-10 10-10-4.486-10-10 4.486-10 10-10zm0-2c-6.627 0-12 5.373-12 12s5.373 12 12 12 12-5.373 12-12-5.373-12-12-12zm2 12l-4.5 4.5 1.527 1.5 5.973-6-5.973-6-1.527 1.5 4.5 4.5z"/></Arrow>
     )
}
const Arrow = styled.svg`
     margin-right: 8px;
     transition: 0.2s;
     transform: rotate(180deg);

     &:hover{
          transform: rotate(0deg);
     }
`;

export default ArrowLink;
